import React from 'react'

import { Switch, Route, useHistory, Link } from 'react-router-dom'

import { useDispatch, useSelector } from "react-redux"
import { login } from "../../actions/authActions"

import Container from 'react-bootstrap/Container'
import Jumbotron from 'react-bootstrap/Jumbotron'
import Form from 'react-bootstrap/Form'
import InputGroup from 'react-bootstrap/InputGroup'
import Button from 'react-bootstrap/Button'
import FormControl from 'react-bootstrap/FormControl'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUser, faLock } from '@fortawesome/free-solid-svg-icons'

export default function Signin(props) {
    const dispatch = useDispatch();
    const auth = useSelector(state => state.auth);
    const error = useSelector(state => state.error);
    const history = useHistory();

    const [username, setUsername] = React.useState('');
    const [password, setPassword] = React.useState('');

    // const [usernameInvalid, setUsernameInvalid] = React.useState(false);
    // const [passwordInvalid, setPasswordInvalid] = React.useState(false);

    const signinSubmit = e => {
        e.preventDefault();
        // setUsernameInvalid(false);
        // setPasswordInvalid(false);
        // if (username === '' || password === '') {
        //     if (username === '') setUsernameInvalid(true);
        //     if (password === '') setPasswordInvalid(true);
        //     console.log('error');
        //     return
        // } else {
        //     console.log('sign in');
        //     dispatch(login({ username, password }));
        // }
        dispatch(login({ username, password }));
    };

    React.useEffect(() => {
        if (auth.isAuthenticated === true) {
            console.log('login success');
            history.push("/");
        }
    }, [auth, history])

    React.useEffect(() => {
        if (Object.keys(error.auth).length !== 0) {
            console.log('auth error');
            console.log(error.auth)
        }
    }, [error])

    return (
        <Container className="p-5">
            <Jumbotron>
                <h1 className="header">Welcome To ABC</h1>
                <Form noValidate onSubmit={signinSubmit}>
                    <Form.Group controlId="formUsername">
                        <InputGroup>
                            <InputGroup.Prepend>
                                <InputGroup.Text>
                                    <FontAwesomeIcon icon={faUser} fixedWidth />
                                </InputGroup.Text>
                            </InputGroup.Prepend>
                            <Form.Control
                                type="text"
                                placeholder="Username"
                                value={username}
                                onChange={(e) => (setUsername(e.target.value))}
                                // isInvalid={usernameInvalid}
                                isInvalid={error.auth.hasOwnProperty('username')}
                                required
                            />
                            <Form.Control.Feedback type="invalid">
                                {
                                    error.auth.hasOwnProperty('username') ? error.auth.username : ''
                                }
                            </Form.Control.Feedback>
                        </InputGroup>
                    </Form.Group>

                    <Form.Group controlId="formPassword">
                        <InputGroup>
                            <InputGroup.Prepend>
                                <InputGroup.Text>
                                    <FontAwesomeIcon icon={faLock} fixedWidth />
                                </InputGroup.Text>
                            </InputGroup.Prepend>
                            <Form.Control
                                type="password"
                                placeholder="Password"
                                value={password}
                                onChange={(e) => (setPassword(e.target.value))}
                                // isInvalid={passwordInvalid}
                                isInvalid={error.auth.hasOwnProperty('password')}
                                required
                            />
                            <Form.Control.Feedback type="invalid">
                                {
                                    error.auth.hasOwnProperty('password') ? error.auth.password : ''
                                }
                            </Form.Control.Feedback>
                        </InputGroup>
                    </Form.Group>
                    <Button variant="primary" type="submit">
                        Sign In
                    </Button>
                </Form>
            </Jumbotron>
        </Container>
    );

}