// const alist = [obj, obj2]

export default function Course(props) {
    const dispatch = useDispatch();
    const history = useHistory();

    const [alist, setAlist] = React.useState([obj, obj2]);
    const [submitStatus, setSubmitStatus] = React.useState({});

    const auth = useSelector(state => state.auth);

    React.useEffect(() => {
        // axios.get()
    }, [])

    const updateAlist = (e, lessonIdx, assessmentIdx, idx, attr) => {
        // let tempObj = alist[lessonIdx];
        // if (attr === 'rate') {
        //     tempObj.assessments[assessmentIdx].grading[idx][attr] = e;
        // } else {
        //     tempObj.assessments[assessmentIdx].grading[idx][attr] = e.target.value;
        // }
        // setAlist(alist.splice(lessonIdx, 1));
        // setAlist([...alist, tempObj])

        // if (attr === 'rate') {
        //     setAlist(alist.map((ele, i) => (i === lessonIdx ? ele.assessments[assessmentIdx].grading[idx][attr] = e : ele)))
        // } else {
        //     setAlist(alist.map((ele, i) => (i === lessonIdx ? ele.assessments[assessmentIdx].grading[idx][attr] = e.target.value : ele)))
        // }
        // let templist = alist.map((ele, i) => {
        //     if (i === lessonIdx) {
        //         if (attr === 'grade') {
        //             ele.assessments[assessmentIdx].grading[idx][attr] = e;
        //         } else {
        //             ele.assessments[assessmentIdx].grading[idx][attr] = e.target.value;
        //         }
        //     }
        //     return ele
        // })
        // console.log(templist)
        setAlist(
            alist.map((ele, i) => {
                if (i === lessonIdx) {
                    if (attr === 'grade') {
                        ele.assessments[assessmentIdx].grading[idx][attr] = e;
                    } else if (attr === 'remarks') {
                        ele.assessments[assessmentIdx].remarks[0].comment = e.target.value;
                    } else if (attr === 'review') {
                        ele.assessments[assessmentIdx].review[0].comment = e.target.value;
                    } else {
                        ele.assessments[assessmentIdx].grading[idx][attr] = e.target.value;
                    }
                }
                return ele
            })
        )
        setSubmitStatus(submitStatus => ({
            ...submitStatus,
            [lessonIdx]: true
        }))
    }

    const submitForm = (lessonIdx) => {
        console.log('submit form')
        setSubmitStatus(submitStatus => ({
            ...submitStatus,
            [lessonIdx]: false
        }))
    }

    return (
        <React.Fragment>
            {alist.map((lesson, lessonIdx) => (
                <Row key={lessonIdx}>
                    <Col>
                        <Card className='m-3'>
                            <Card.Header as="h5">{lesson.lessonName}</Card.Header>
                            <Card.Body>
                                <Tabs defaultActiveKey={0} style={{ borderBottom: '1px #eee solid' }} className='my-3'>
                                    {lesson.assessments.map((assessment, assessmentIdx) => (
                                        <Tab eventKey={assessmentIdx} title={assessment.studentName} key={assessmentIdx} className='my-3 mx-1'>
                                            <Form>
                                                {assessment.grading.map((item, idx) => (
                                                    <React.Fragment key={idx}>
                                                        <Form.Group as={Row} style={{ borderBottom: '1px #eee solid' }} className='p-3' >
                                                            <Form.Label column sm={6}>
                                                                {item.description}
                                                            </Form.Label>
                                                            <Col sm={6}>
                                                                <Rating initialRating={item.grade} emptySymbol={<FontAwesomeIcon icon={faStar} color="#e6e600" />} fullSymbol={<FontAwesomeIcon icon={faSolidStar} color="#e6e600" />} onChange={(rate) => updateAlist(rate, lessonIdx, assessmentIdx, idx, 'grade')} />
                                                            </Col>
                                                        </Form.Group>
                                                    </React.Fragment>
                                                ))}
                                                <Form.Group as={Row} className='p-3' >
                                                    <Col sm={12}>
                                                        <Form.Control type="text" placeholder="remarks" as='textarea' readOnly={lesson.tutor.includes(auth.user._id) ? false : true} value={assessment.remarks[0].comment === '' ? 'No comment on remarks' : assessment.remarks[0].comment} onChange={(e) => updateAlist(e, lessonIdx, assessmentIdx, null, 'remarks')} />
                                                    </Col>
                                                </Form.Group>
                                                <Form.Group as={Row} className='p-3' >
                                                    <Col sm={12}>
                                                        <Form.Control type="text" placeholder="review" as='textarea' readOnly={lesson.reviewer.includes(auth.user._id) ? false : true} value={assessment.review[0].comment === '' ? 'No comment on review' : assessment.review[0].comment} onChange={(e) => updateAlist(e, lessonIdx, assessmentIdx, null, 'review')} />
                                                    </Col>
                                                </Form.Group>
                                                <Button
                                                    variant="success"
                                                    //  type="submit"
                                                    disabled={submitStatus.hasOwnProperty(lessonIdx) ? submitStatus[lessonIdx] === true ? false : true : true}
                                                    onClick={() => submitForm(lessonIdx)}
                                                >
                                                    {/* submitStatus.hasOwnProperty(lessonIdx) ? submitStatus[lessonIdx] === true ? 'Update & Save' : 'No change detected' : 'No change detected' */}
                                                    Update &amp; Save
                                                </Button>
                                            </Form>
                                        </Tab>
                                    ))}
                                </Tabs>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            ))}
        </React.Fragment >
    );

}