import {
    UPDATE_AUTH_ERRORS,
    CLEAR_AUTH_ERRORS
} from '../actions/types';

const initialState = {
    auth: {},
};

export default function (state = initialState, action) {
    switch (action.type) {
        // case GET_ERRORS:
        //     return {
        //         msg: action.payload.msg,
        //         status: action.payload.status,
        //         id: action.payload.id,
        //     };
        // case CLEAR_ERRORS:
        //     return {
        //         msg: {},
        //         status: null,
        //         id: null
        //     };
        case UPDATE_AUTH_ERRORS:
            return {
                ...state,
                auth: action.payload
            };
        case CLEAR_AUTH_ERRORS:
            return {
                ...state,
                auth: {},
            };
        default:
            return state;
    }
}