import { GET_ERRORS, CLEAR_ERRORS, UPDATE_AUTH_ERRORS, CLEAR_AUTH_ERRORS } from './types';

// RETURN ERRORS
export const returnErrors = (msg, status, id = null) => {
    return {
        type: GET_ERRORS,
        payload: { msg, status, id }
    };
};

// CLEAR ERRORS
export const clearErrors = () => {
    return {
        type: CLEAR_ERRORS
    };
};

// UPDATE AUTH ERRORS
export const updateAuthErrors = (data) => {
    return {
        type: UPDATE_AUTH_ERRORS,
        payload: data
    };
};

// CLEAR AUTH ERRORS
export const clearAuthErrors = () => {
    return {
        type: CLEAR_AUTH_ERRORS
    };
};