import React from 'react'

import { Switch, Route, useHistory, Link } from 'react-router-dom'

import { useDispatch, useSelector } from "react-redux"
import { logout } from "../actions/authActions"

import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import Button from 'react-bootstrap/Button'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHome, faUserGraduate, faChalkboardTeacher, faUserTie, faSignOutAlt } from '@fortawesome/free-solid-svg-icons'

const navLinkList = [
    { to: "/", icon: faHome, text: "Home" },
    { to: "/", icon: faUserTie, text: "Tutor" },
    { to: "/", icon: faChalkboardTeacher, text: "Class" },
    { to: "/signin", icon: faUserGraduate, text: "Student" },
]

export default function TopNavbar(props) {
    const dispatch = useDispatch();
    const auth = useSelector(state => state.auth);
    const history = useHistory();

    const [activeNavLink, setActiveNavLink] = React.useState(0);

    const signoutBtnClick = () => {
        dispatch(logout());
    }

    return (
        <Navbar
            // collapseOnSelect
            bg="dark"
            variant="dark"
            expand="lg"
            sticky="top"
        >
            <Navbar.Brand>Home</Navbar.Brand>
            {
                auth.isAuthenticated === true ? (
                    <React.Fragment>
                        <Nav className="mr-auto">
                        </Nav>
                        <Nav>
                            <Button variant="outline-warning" onClick={() => signoutBtnClick()}><FontAwesomeIcon icon={faSignOutAlt} fixedWidth />Sign Out</Button>
                        </Nav>
                    </React.Fragment>
                ) : null}

            {/* <Navbar.Toggle aria-controls="responsive-navbar-nav" /> */}
            {/* <Navbar.Collapse id="responsive-navbar-nav"> */}
            {/* <Nav className="mr-auto"> */}
            {/* <Nav.Item>a</Nav.Item> */}
            {/* <Nav.Item>b</Nav.Item> */}
            {/* </Nav> */}
            {/* <Nav activeKey={activeNavLink} onSelect={(selectedKey) => setActiveNavLink(selectedKey)}> */}
            {/* <Nav.Link as={Link} to="/" eventKey="link-home"><FontAwesomeIcon icon={faHome} fixedWidth />Home</Nav.Link> */}
            {/* <Nav.Link as={Link} to="/signin" eventKey="link-signin"><FontAwesomeIcon icon={faUserGraduate} fixedWidth />Student</Nav.Link> */}
            {
                // navLinkList.map((e, i) => (
                //     <Nav.Link as={Link} to={e.to} eventKey={i}><FontAwesomeIcon icon={e.icon} fixedWidth />{' '}{e.text}</Nav.Link>
                // )
                // )
            }
            {/* </Nav> */}
            {/* </Navbar.Collapse> */}
        </Navbar>
    );

}